"use client"

import CodeViewer from "@/components/common/CodeViewer";
import CustomTextField from "@/components/common/CustomTextField"
import { yupResolver } from "@hookform/resolvers/yup";
import { Box, Button } from "@mui/material"
import { useState } from "react";
import { useForm } from "react-hook-form";
import * as yup from "yup";

const schema = yup.object({
    outlined: yup.string().required("Outlined is required"),
    filled: yup.string().required("Filled is required"),
    standard: yup.string().required("Standard is required"),
    outLinedWithLabel: yup.string().required("outLinedWithLabel  is required"),
    filledWithLabel: yup.string().required("filledWithLabel  is required"),
    standardWithLabel: yup.string().required("standardWithLabel is required"),
})

type DiffVariantForm = yup.InferType<typeof schema>



export default function DiffVariant() {
    const form = useForm<DiffVariantForm>({
        resolver: yupResolver(schema),
        defaultValues: {
            outlined: "",
            filled: "",
            standard: "",
            outLinedWithLabel: "",
            filledWithLabel: "",
            standardWithLabel: "",
        }
    })


    const [DiffVariantCode, setDVCode] = useState<string>(`
          <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                            <CustomTextField
                                placeholder="outlined"
                                form={form}
                                formFieldName="outlined"
                                fullWidth={false}
                                useLabel={false}
                                inputTitle="Outlined"
                            />
                            <CustomTextField
                                placeholder="OutLined With Label"
                                form={form}
                                formFieldName="outLinedWithLabel"
                                fullWidth={false}
                                useLabel={true}
                                label="OutLined With Label"
                            />
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                            <CustomTextField
                                placeholder="filled"
                                form={form}
                                formFieldName="filled"
                                fullWidth={false}
                                useLabel={false}
                                inputTitle="Filled"
                            />
                            <CustomTextField
                                placeholder="Filled With Label"
                                form={form}
                                formFieldName="filledWithLabel"
                                fullWidth={false}
                                useLabel={true}
                                label="Filled With Label"
                            />
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                            <CustomTextField
                                placeholder="Standard"
                                form={form}
                                formFieldName="standard"
                                fullWidth={false}
                                useLabel={false}
                                inputTitle="Standard"
                            />
                            <CustomTextField
                                placeholder="Standard With Label"
                                form={form}
                                formFieldName="standardWithLabel"
                                fullWidth={false}
                                useLabel={true}
                                label="Standard With Label"
                            />
                        </Box>
    `)

    const changeDiff = (content: string) => {
        setDVCode(content);
    }

    const handleSubmit =(data: DiffVariantForm) =>{
        console.log(data)
    }

    return (
        <>
            <Box
                sx={{
                    mt: 4,
                    minWidth: '856px', // Adjust width as needed
                    borderRadius: '8px',
                    minHeight:"200px"
                }} >
                <h2>Different variant TextField</h2>
                <br />
                There are three variants with label or Input title: outlined (default), filled, and standard.
                <br />
                They also have error handling, you can click the submit without any typing to review it.
                <Box sx={{ marginTop: "15px", display: "flex", gap: "10px" }} minHeight={"180px"}>


                    <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                        <CustomTextField
                            placeholder="outlined"
                            form={form}
                            formFieldName="outlined"
                            fullWidth={false}
                            useLabel={false}
                            inputTitle="Outlined"
                        />
                        <CustomTextField
                            placeholder="OutLined With Label"
                            form={form}
                            formFieldName="outLinedWithLabel"
                            fullWidth={false}
                            useLabel={true}
                            label="OutLined With Label"
                        />
                    </Box>
                    <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                        <CustomTextField
                            placeholder="filled"
                            form={form}
                            formFieldName="filled"
                            fullWidth={false}
                            useLabel={false}
                            inputTitle="Filled"
                        />
                        <CustomTextField
                            placeholder="Filled With Label"
                            form={form}
                            formFieldName="filledWithLabel"
                            fullWidth={false}
                            useLabel={true}
                            label="Filled With Label"
                        />
                    </Box>
                    <Box sx={{ display: "flex", flexDirection: "column", gap: "10px" }}>
                        <CustomTextField
                            placeholder="Standard"
                            form={form}
                            formFieldName="standard"
                            fullWidth={false}
                            useLabel={false}
                            inputTitle="Standard"
                        />
                        <CustomTextField
                            placeholder="Standard With Label"
                            form={form}
                            formFieldName="standardWithLabel"
                            fullWidth={false}
                            useLabel={true}
                            label="Standard With Label"
                        />
                    </Box>
                </Box>
                <Button type="submit" onClick={form.handleSubmit(handleSubmit)} variant="outlined">Submit</Button>
            </Box>
            <Box
                sx={{
                    mt: 2,
                    minWidth: '856px', // Adjust width as needed
                    borderRadius: '8px',
                    overflow: 'hidden', // To handle inner borders cleanly
                }}
            >
                <CodeViewer
                    content={DiffVariantCode}
                    setContent={changeDiff}
                />
            </Box>
        </>
    )
}